@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500&display=swap);
body {
  margin: 0;
  position: relative;
  font-family: 'Ubuntu', sans-serif;
  overflow: hidden;
}
a {
  color: black;
  font-weight: 500;
}
::selection {
  background: #bd9999; /* WebKit/Blink Browsers */
  color:white;
}
::-moz-selection {
  background: #bd9999; /* Gecko Browsers */
  color:white;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: white;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #b3b3b3;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b3b3b3;
}
.content-container{
  background-color: white;
  color: black;
  position: absolute;
  width: 700px;
  height: 100vh;
  left:15vw;
  z-index: 1;
  top:0;
}
.content-container-mobile{
  background-color: white;
  color: black;
  position: absolute;
  width: 100vw;
  height: 100vh;
  left:5vw;
  z-index: 1;
  top:0;
}
.content{
  height: 80%;
  padding-left: 3vw;

  padding-right: 3vw;

  overflow-y:scroll;
  overflow-x: hidden;
}
.content-mobile{
